export default function Page({ params }: { params: { id: string } }) {
  const orderId = Number(params.id);

  if (Number.isNaN(orderId)) {
    return <div>Invalid Order ID</div>;
  }

  return (
    <ProtectedRoute>
      <AdminOrderDetailPageWrapper orderId={orderId} />
    </ProtectedRoute>
  );
}
